.delivery-teaser {
	&__container {
		max-width: 844px; }
	&__pic {
		margin: 15px 0 0 -10px;
		@include r(599) {
			margin: 15px -10px 0; } }
	&__row {
		margin: -30px 0 20px;
		text-align: center;
		@include sm {
			margin: 0; }
		@include r(599) {
			margin-top: 15px; } }
	&__btn {
		padding: 0 27px; }
	&_default {
		padding: 65px 0 160px;
		@include md {
			padding: 65px 0 80px; }
		@include sm {
			padding: 50px 0; }
		.next-section {
			bottom: 11%;
			@include md {
				bottom: 1%; } } } }

