.achieve {
	&__container {
		@include xs {
			padding: 0 15px; } }
	&__slider {
		margin: 35px auto 0;
		@include r(1365) {
			max-width: 930px; }
		@include sm {
			max-width: 680px; }
		@include xs {
			max-width: 350px; }
		&-item {
			display: block;
			padding: 5px;
			outline: 3px solid transparent;
			@include tr(outline .3s);
			background: $white;
			font-size: 0;
			@include hover {
				outline-color: $red; } }
		&-text {
			display: inline-block;
			vertical-align: middle;
			width: calc(100% - 120px);
			padding: 0 15px;
			text-align: center;
			line-height: 1.3;
			color: $black;
			@include r(1365) {
				padding: 0 10px;
				font-size: 14px; } }
		&-title {
			font-size: 22px;
			font-weight: 500;
			@include r(1365) {
				font-size: 18px; }
			@include xs {
				font-size: 16px; } }
		&-descr {
			margin-top: 5px;
			font-size: 15px;
			@include r(424) {
				font-size: 13px; } }
		&-pic {
			display: inline-block;
			vertical-align: middle;
			width: 120px;
			height: 170px;
			img {
				height: 100%; } }
		.owl-item {
			padding: 3px; }
		.owl-carousel__btn_next {
			right: -11.5%;
			@include r(1439) {
				right: -9%; }
			@include r(1365) {
				right: -4%; }
			@include sm {
				right: -5.5%; }
			@include xs {
				right: -8.5%; }
			@include r(424) {
				right: -4.5%; } }
		.owl-carousel__btn_prev {
			left: -11.5%;
			@include r(1439) {
				left: -9%; }
			@include r(1365) {
				left: -4%; }
			@include sm {
				left: -5.5%; }
			@include xs {
				left: -8.5%; }
			@include r(424) {
				left: -4.5%; } }
		.owl-carousel__btn-icon, .owl-carousel__btn-icon {
			font-size: 105px;
			@include xs {
				font-size: 80px; }
			@include r(424) {
				font-size: 38px; } } }
	&_default {
		padding: 68px 0 80px;
		@include sm {
			padding: 50px 0 60px; } } }
