.header {
	padding: 62px 0 32px;
	color: $black;
	@include md {
		padding: 20px 0; }
	@include xs {
		position: relative; }
	&_no-slider {
		padding: 62px 0 53px;
		border-bottom: 2px solid #e6e7e8; }
	&__container {
		max-width: 1142px;
		font-size: 0;
		white-space: nowrap;
		@include md {
			text-align: center; }
		@include sm {
			padding: 0 10px; }
		@include xs {
			display: flex;
			flex-wrap: wrap; } }
	&__col {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		height: 42px;
		padding: 0 13px;
		@include vertical;
		@include md {
			padding: 0 6px; }
		@include xs {
			padding: 0;
			z-index: 11;
			height: auto;
			&::before {
				display: none; } }
		&:first-child {
			padding-left: 0; }
		&:last-child::after {
			display: none; }
		&::after {
			content: '';
			position: absolute;
			right: -1px;
			top: 4px;
			background: #e6e7e8;
			height: 41px;
			width: 1px;
			@include md {
				top: 2px; }
			@include xs {
				display: none; } }
		&:nth-child(1) {
			@include xs {
				flex-basis: 100%;
				margin-bottom: 25px;
				text-align: left; } }
		&:nth-child(3) {
			@include sm {
				display: none; }
			@include xs {
				display: block; } } }
	&__phone::after {
		top: 5px; }
	&__logo {
		display: inline-block;
		vertical-align: middle;
		@include s('logo'); }
	&__phone {
		display: inline-block;
		vertical-align: middle;
		text-align: center;
		&-link {
			display: block;
			font-size: 22px;
			font-weight: 500;
			@include md {
				font-size: 18px; }
			@include xs {
				line-height: 1;
				font-size: 20px; } }
		&-text {
			color: $grey;
			font-size: 13px;
			font-weight: 300;
			@include md {
				font-size: 12px; } } }
	&__bar {
		display: inline-flex;
		padding-right: 0;
		@include md {
			padding-right: 0; }
		@include sm {
			padding-right: 0; }
		@include xs {
			display: flex;
			align-items: flex-end;
			padding-bottom: 5px;
			margin-left: auto; }
		&-col {
			position: relative;
			display: inline-flex;
			align-items: center;
			padding-right: 24px;
			height: 42px;
			@include md {
				padding-right: 16px; }
			@include sm {
				padding-right: 8px; }
			@include xs {
				height: auto; }
			&::after {
				@extend .header__col::after;
				right: 10px;
				top: 4px;
				@include md {
					right: 6px; }
				@include sm {
					display: none; } }
			&:last-child {
				padding-right: 0; }
			&:last-child::after {
				display: none; } }
		&-link {
			position: relative;
			display: block;
			&_mail {
				@include s('mail'); }
			&_cart {
				@include s('cart'); }
			&_user {
				@include s('user'); } } }

	&__title {
		@include hide-text; } }

.nav {
	font-size: 0;
	&.is-open {
		pointer-events: auto;
		opacity: 1;
		transform: scale(1); }
	@include xs {
		pointer-events: none;
		opacity: 0;
		position: fixed;
		top: 116px;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 10;
		display: flex;
		justify-content: center;
		flex-direction: column;
		overflow: auto;
		transition: opacity .25s ease-in-out, transform .3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
		transform: scaleX(.6);
		background: $white;
		box-shadow: inset 0 0 10px 5px rgba(0,0,0,0.35); }
	&__link {
		position: relative;
		display: inline-block;
		padding: 1px;
		vertical-align: middle;
		font-size: 16px;
		font-weight: 700;
		border-bottom: 2px solid  #e7e8e9;
		@include xs {
			display: block;
			padding: 25px 10px;
			font-size: 18px;
			line-height: 1; }
		@include sm {
			font-size: 14px; }
		@include rh(374) {
			padding: 10px!important; }
		&::after {
			content: '';
			position: absolute;
			left: 50%;
			right: 50%;
			bottom: -2px;
			background: #e7e8e9;
			height: 2px;
			@include tr(.3s ease); }
		@include hover {
			&::after {
				left: 0;
				right: 0;
				background: $red; } }
		&:not(:last-child) {
			margin-right: 22px;
			@include md {
				margin-right: 11px; }
			@include sm {
				margin-right: 12px; }
			@include xs {
				margin-right: 0; } }
		&.is-active {
			border-bottom-color: $red;
			&::after {
				background: $red; } } } }
