.ask {
	&__container {
		max-width: 910px;
		@include xs {
			max-width: 400px; } }
	&__descr {
		text-align: center;
		font-size: 20px;
		line-height: 33px;
		color: $black;
		@include xs {
			font-size: 17px; } }
	&__form-in {
		margin-top: 17px;
		margin-left: 75px;
		@include sm {
			margin-left: 0; }
		@include clr; }
	&__form-col {
		float: left;
		width: percentage(258 / 805);
		@include xs {
			width: 100%; }
		&:nth-child(3) {
			padding-right: 21px;
			text-align: center;
			@include sm {
				padding-right: 0; } }
		& + & {
			margin-left: 15px;
			@include sm {
				margin-left: 13px; }
			@include xs {
				margin-left: 0;
				margin-top: 15px; } } }
	&__form-field {
		& + & {
			margin-top: 15px; } }
	&__form-message {
		min-height: 105px; }
	&__form-descr {
		margin-top: 13px;
		font-size: 13px; }

	&_default {
		padding: 45px 0 60px;
		@include sm {
			padding: 40px 0 50px; } } }
