	----------------------------------- */
.count {
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #d62326;
  color: #fff;
  font-size: 8px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  border-radius: 50%;
}

