  top: -8px;
  right: -15px;
}

/* rating
	----------------------------------- */
.rating__in {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.rating__input {
  display: none;
}

.rating__input ~ .rating__icon .rating__star {
  fill: #cccecf;
}

.rating__input:checked ~ .rating-icon .rating__star,
.rating__input:checked .rating-icon .rating__star {
  fill: #df4d4d;
}

.rating__icon:hover .rating__star,
.rating__icon:hover ~ .rating__icon .rating__star {
  fill: #cccecf;
}

.rating__input:checked .rating__icon:hover .rating__star,
.rating__input:checked ~ .rating__icon .rating__star {
  fill: #df4d4d;
}

.rating__icon {
  display: inline-block;
  cursor: pointer;
}

.rating__star {
  font-size: 16px;
}

.rating.is-disabled {
  pointer-events: none;
}

