.team {
	&__in {
		margin: 45px 0 30px;
		@include clr;
		@include r(1365) {
			max-width: 840px;
			margin: 35px auto 25px; }
		@include sm {
			max-width: 660px; } }
	&__col {
		float: left;
		@include xs {
			float: none; }
		&:nth-child(1) {
			width: percentage(350 / 1110);
			margin-right: 30px;
			@include r(1365) {
				margin-right: 20px;
				width: 36.5%; }
			@include sm {
				width: 41.5%;
				margin-right: 15px; }
			@include xs {
				width: 100%;
				max-width: 320px;
				margin: 0 auto 30px; } }
		&:nth-child(2) {
			width: percentage(730 / 1110);
			@include r(1365) {
				width: 61%; }
			@include sm {
				width: 56%; }
			@include xs {
				width: 100%;
				max-width: 340px;
				margin: 0 auto; } } }
	&__lead {
		&-pic {
			padding-top: 104%;
			@include cover; }
		&-text {
			position: relative;
			margin-top: 35px;
			padding: 0 20px 15px 28px;
			font-size: 15px;
			@include sm {
				margin-top: 25px;
				padding: 0 16px 15px 20px;
				font-size: 14px; } }
		&-title {
			color: $black;
			font-weight: 700; }
		&-descr {
			line-height: 25px; }
		&-quote {
			position: absolute;
			fill: $red;
			font-size: 13px;
			&_l {
				top: 0;
				left: 0; }
			&_r {
				right: 0;
				bottom: 10%; } }
		&-name {
			margin-top: 8px;
			font-weight: 500;
			color: $black; } }
	&__slider {
		@include xs {
			padding: 0 10px; }
		&-item {
			@include r(424) {
				text-align: center; } }
		.owl-carousel__btn {
			top: 49%;
			@include r(424) {
				top: 33%; } }
		.owl-carousel__btn_prev {
			left: -71%;
			@include r(1439) {
				left: -66%; }
			@include r(1365) {
				left: -76%; }
			@include sm {
				left: -91%; }
			@include xs {
				left: -10%; }
			@include r(424) {
				left: -2%; } }
		.owl-carousel__btn_next {
			right: -19%;
			@include r(1439) {
				right: -14%; }
			@include sm {
				right: -12%; }
			@include xs {
				right: -10%; }
			@include r(424) {
				right: -2%; } }
		.owl-carousel__btn-icon {
			font-size: 141px;
			@include xs {
				font-size: 80px; }
			@include r(424) {
				font-size: 46px; } } }
	&__person {
		margin-bottom: 15px;
		text-align: center;
		@include r(424) {
			display: inline-block; }
		&-pic {
			margin: 0 auto;
			width: 160px;
			height: 213px;
			@include r(424) {
				width: 134px;
				height: 180px; }
			img {
				height: 100%; } }
		&-data {
			padding: 10px 0 0;
			line-height: 25px;
			@include r(424) {
				font-size: 14px;
				line-height: 1.5; } }
		&-name {
			font-weight: 500; }
		&-position {
			font-weight: 400; }
		&-tel {
			display: block;
			margin-bottom: 2px;
			font-weight: 400; }
		&-mail {
			font-weight: 500; } }
	&_default {
		padding: 60px 0 130px;
		.next-section {
			bottom: 8%;
			@include r(1365) {
				bottom: 2%; } }
		@include r(1365) {
			padding: 60px 0 80px; }
		@include sm {
			padding: 50px 0 30px; } } }

