.benefits {
	&__list {
		margin: 58px 0 45px;
		font-size: 0;
		text-align: center;
		@include sm {
			margin: 40px 0; }
		@include xs {
			margin: 25px 0; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 0 20px 45px;
		text-align: center;
		width: 33.33333%;
		@include sm {
			padding: 0 10px 20px; }
		@include xs {
			width: 50%; }
		@include r(599) {
			width: 100%;
			padding: 0 10px 10px; }
		&-pic {
			height: 90px;
			margin-bottom: 5px;
			@include vertical;
			@include sm {
				margin-bottom: 4px; }
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle;
				margin: 0 auto; }
			&_tape::after {
				@include s('tape'); }
			&_peoples::after {
				@include s('peoples'); }
			&_building::after {
				@include s('building'); }
			&_vehicle::after {
				vertical-align: bottom;
				@include s('vehicle'); }
			&_handshake::after {
				@include s('handshake'); }
			&_pc-shield::after {
				@include s('pc-shield'); }
			&_marker::after {
				@include s('marker'); }
			&_pen-ruler::after {
				@include s('pen-ruler'); } }
		&-title {
			margin-bottom: 5px;
			font-size: 22px;
			font-weight: 700;
			line-height: 1;
			color: $black;
			@include sm {
				font-size: 19px; } }
		&-sub-title {
			margin-bottom: 5px;
			font-size: 16px;
			font-weight: 500;
			color: $grey; }
		&-descr {
			font-size: 16px;
			color: $grey; } }
	&_default {
		padding: 65px 0 45px;
		@include sm {
			padding: 50px 0 10px; } } }
