.testimonials {
	@include sm {
		padding: 30px 0; }
	&__container {
		max-width: 1200px;
		@include clr; }
	&__col {
		float: left;
		width: 50%;
		@include sm {
			width: 100%;
			float: none; }
		&:nth-child(1) {
			padding-left: 15px;
			@include md {
				padding-left: 0; }
			@include sm {
				max-width: 600px;
				margin: 0 auto; }
			@include xs {
				max-width: 400px; } } }
	&__btn {
		padding: 0 5px;
		width: 218px; }
	&__slider {
		&-item {
			position: relative;
			padding-top: 127.7%;
			z-index: 0;
			cursor: grab; }
		&-title {
			position: absolute;
			top: 9.7%;
			left: 0;
			right: 0;
			z-index: 1;
			padding: 0 15px;
			line-height: 1.3;
			mark {
				display: block;
				color: $red;
				background: transparent; } }
		&-img {
			@include coverdiv;
			@include cover;
			z-index: -1; }
		&-btn {
			position: absolute;
			left: 50%;
			bottom: 11%;
			transform: translateX(-50%);
			z-index: 1;
			@include md {
				bottom: 6%; }
			@include xs {
				transform: none;
				left: 0;
				right: 0;
				max-width: 220px;
				margin: 0 auto; } }
		.owl-carousel__btn_prev {
			left: 4.5%; }
		.owl-carousel__btn_next {
			right: 4.5%; }
		.owl-carousel__btn-icon {
			font-size: 43px;
			fill: none;
			stroke: $black; } }
	&__block {
		padding: 45px 30px 10px 10px;
		@include md {
			padding: 40px 0 15px 15px; }
		@include sm {
			padding: 15px; }
		@include xs {
			padding: 15px 0; }
		&-item {
			position: relative;
			padding: 25px 30px;
			border-bottom: 1px dashed #e0e1e2;
			@include md {
				padding: 15px 20px; } }
		&-text {
			text-align: justify;
			@include sm {
				font-size: 15px;
				text-align: left; } }
		&-link {
			font-size: 12px;
			font-weight: 700;
			letter-spacing: .07em; }
		&-name {
			display: inline-block;
			vertical-align: middle;
			margin-left: 25px;
			font-weight: 500;
			@include r(424) {
				margin-left: 5px;
				font-size: 15px; } }
		&-row {
			margin-top: 10px;
			@include r(424) {
				margin: 5px -25px 0;
				text-align: center; } }
		&-quote {
			position: absolute;
			fill: $red;
			font-size: 13px;
			&_l {
				top: 17%;
				left: 1%;
				@include md {
					left: 0; }
				@include xs {
					top: 18px;
					left: -1%; } }
			&_r {
				right: 1%;
				bottom: 34%;
				@include md {
					right: 0; }
				@include xs {
					bottom: 30%;
					right: -1%; } } }
		&-bottom {
			margin: 26px 0 0;
			text-align: center; } } }
