.footer {
	padding: 50px 0 110px;
	@include md {
		padding: 50px 0 60px; }
	@include xs {
		padding: 50px 0 70px; }
	&__container {
		position: relative;
		display: flex;
		justify-content: space-between;
		@include sm {
			flex-wrap: wrap; }
		@include xs {
			justify-content: space-around; } }
	&__col {
		flex: 0 1 180px;
		padding-bottom: 60px;
		text-align: left;
		@include xs {
			flex: 0 1 260px;
			padding-bottom: 25px;
			margin: 0 10px; }
		@include r(599) {
			flex-basis: 252px; }
		&:nth-child(5) {
			position: relative;
			flex-basis: 168px;
			@include sm {
				flex-basis: 100%;
				padding: 15px 0 80px;
				text-align: center;
				.footer__title {
					text-align: center; } }
			@include xs {
				padding: 0; } } }
	&__title {
		margin-bottom: 12px;
		text-align: left;
		letter-spacing: .07em;
		@include sm {
			font-size: 16px;
			letter-spacing: 0; }
		@include xs {
			font-size: 18px; } }
	&__list {
		padding-right: 10px;
		@include sm {
			padding-right: 15px; }
		@include xs {
			padding-right: 0; }
		li {
			line-height: 20px; }
		li:not(:last-child) {
			margin-bottom: 10px; }
		a {
			font-size: 14px;
			line-height: 18px;
			text-decoration: underline;
			@include sm {
				font-size: 14px; }
			@include hover {
				text-decoration: none; } } }
	&__address {
		padding-bottom: 0;
		font-style: normal;
		font-size: 14px;
		@include sm {
			font-size: 14px; }
		&-item:not(:last-child) {
			margin-bottom: 8px; }
		&-city {
			font-weight: 700;
			text-transform: uppercase;
			color: $red; }
		&-str {
			margin: 3px 0; }
		&-tel {
			font-weight: 500; } }
	&__mail-link {
		display: block;
		margin: 17px 0 10px;
		text-align: center;
		font-size: 14px; }
	&__write-btn {
		height: 41px;
		width: 100%;
		max-width: 168px;
		margin: 0 auto;
		line-height: 41px; }
	&__dev-link {
		position: absolute;
		right: 50px;
		bottom: 0;
		display: block;
		padding-left: 16px;
		font-size: 14px;
		line-height: 18px;
		@include sm {
			right: 15px; }
		@include xs {
			right: calc(50% - 61px);
			bottom: -50px;
			text-align: center; }
		&::before {
			content: '';
			position: absolute;
			top: -5px;
			left: -8px;
			@include s('akticom'); }
		span {
			display: block;
			text-decoration: underline; }
		&:hover span {
			text-decoration: none; } }
	&__copy {
		position: absolute;
		left: 15px;
		bottom: 0;
		font-size: 13px;
		color: $black;
		@include xs {
			position: static;
			margin-top: 15px;
			text-align: center; } } }
